<template lang="md">
  # Alert

  ---

  

  ## 

  - 
  - 

  ## API

  |         |                                                       |         |  |
  |----------- |---------------------------------------------------------  | ---------- |-------|
  | type       | `success``info``warning``danger`   | String     |     |
  | message    |                                        | String     |     |
  | closable   |                                   | Boolean   |     |
  | onClose     |                                 | Function   |     |
  | closeText  |                                      | slot     |     |

  ## 

  <demo>
    <example title="">
      <v-alert type="success" message=""></v-alert>
    </example>
    <example title="">
      <v-alert type="success" message=""></v-alert>
      <v-alert type="info" message=""></v-alert>
      <v-alert type="warning" message=""></v-alert>
      <v-alert type="danger" message=""></v-alert>
    </example>
    <example title="">
      <v-alert type="warning" message="" :closable="true"></v-alert>
      <v-alert type="danger" message="" :closable="true"></v-alert>
    </example>
    <example title="">
      <v-alert type="success" message=""></v-alert>
      <v-alert type="info" message=""></v-alert>
      <v-alert type="warning" message=""></v-alert>
      <v-alert type="danger" message=""></v-alert>
    </example>
    <example title="">
      <v-alert type="info" message="" :closable="true" close-text=""></v-alert>
      <v-alert type="danger" message="" :closable="true" close-text=""></v-alert>
    </example>
  </demo>
</template>
<script>
import vAlert from '../../components/alert'
export default{
  components: {
    vAlert
  }
}
</script>



