<template lang="md">
  # InputNumber

  ---

  

  ## 

  

  ## API

  

  |         |            |                |        |
  |-------------|----------------|--------------------|--------------|
  | min     |    | Number | -Infinity        |
  | max     |        | Number      | Infinity           |
  | value     |        | Number      |       1     |
  | step     |        | Number      |      1      |
  | defaultValue     |        | Number      |            |
  | onChange     |        | Function      |            |
  | disabled     |        | Boolean      |      false      |
  | size    |   | String      |            |

  ## 

  <demo>
    <example title="">
      <v-input-number :min="1" :max="10"></v-input-number>
    </example>
    <example title="">
      <v-input-number :min="1" :max="10" size="small"></v-input-number>
      <v-input-number :min="1" :max="10"></v-input-number>
      <v-input-number :min="1" :max="10" size="big"></v-input-number>
    </example>
    <example title="">
      <v-input-number :default-value="10" :disabled="true"></v-input-number>
    </example>
    <example title="">
      <v-input-number :step="10"></v-input-number>
    </example>
  </demo>
</template>
<script>
  import vInputNumber from '../../components/inputnumber'
  export default{
    components: {
      vInputNumber
    }
  }
</script>



