<template lang="md">
  # Switch

  ---

  

  ## 

  - /
  -  `checkbox ` `switch`  `checkbox` 


  ## API

  |      |   |        |    |   |
  |---------|------|------------|---------|--------|
  | checked |  | boolean  |  | false |
  | defaultChecked |  | boolean |  | false |
  | onChange |  | Function(checked:boolean) |  |  |
  | slot::checkedChildren |  | slot node |   |   |
  | slot::unCheckedChildren |  | slot node |   |  |  |

  ## 
  <demo>
    <example title="">
      <v-switch :on-change="showValue"></v-switch>
      <v-switch :checked.sync="defaultChecked" :on-change="showValue"></v-switch>
    </example>
    <example title="">
      <v-switch :disabled="true"></v-switch>
    </example>
    <example title="">
      <v-switch :disabled="disabled" :on-change="showValue">
        <span slot="checkedChildren"></span>
        <span slot="unCheckedChildren"></span>
      </v-switch>
      <v-switch :disabled="disabled" :on-change="showValue">
        <span slot="checkedChildren">
          <i class="icon icon-check"></i>
        </span>
        <span slot="unCheckedChildren">
          <i class="icon icon-cross"></i>
        </span>
      </v-switch>
    </example>
</template>
<script>
import vSwitch from '../../components/switch'
export default {
  data () {
    return {
      defaultChecked: true
    }
  },
  components: {
    vSwitch
  },
  methods: {
    showValue (val) {
      console.log('' + val)
    }
  }
}
</script>



