<template lang="md">
  # Steps

  ---

  

  ## 

  

  ## API

  ### Steps

  

  |       |                                      |        |   | |
  |-----------|------------------------------------------|------------|-------|--------|
  |  current | 0Stepstatus  | number |   | 0 |
  |  size |  | string    |  small, default | default    |
  |  direction |  | string    |  vertical |     |
  |  maxDescriptionWidth |  | number |  | 100 |

  ### Steps.Step

  

  |       |                                      |        |   | |
  |-----------|------------------------------------------|------------|-------|--------|
  |  status | Stepscurrent | string    |  wait, process, finish | wait |
  |  title   |  | string/jsx |  |      |
  |  description |  | string/jsx |  |   |
  |  icon    | Icon | string/jsx |   |  |

  ## 
  <demo>
    <example title="">
      <v-steps :current="1">
        <v-step title="" description=""></v-step>
        <v-step title="" description=""></v-step>
        <v-step title="" description=""></v-step>
        <v-step title="" description=""></v-step>
      </v-steps>
    </example>
    <example title="">
      <v-steps size="small" :current="1">
        <v-step title="" status="finish"></v-step>
        <v-step title="" status="process"></v-step>
        <v-step title="" status="wait"></v-step>
        <v-step title="" status="wait"></v-step>
      </v-steps>
    </example>
    <example title="ICON">
      <v-steps>
        <v-step title="1" status="finish" icon="cloud"></v-step>
        <v-step title="2" status="process" icon="apple"></v-step>
        <v-step title="3" status="wait" icon="github"></v-step>
      </v-steps>
    </example>
    <example title="">
      <div> {{currentStep + 1}} </div>
      <br>
      <v-steps :current="currentStep">
        <v-step title="1"></v-step>
        <v-step title="2"></v-step>
        <v-step title="3"></v-step>
      </v-steps>
      <br>
      <div>
        <v-button type="primary" @click="_next"></v-button>
      </div>
    </example>
    <example title="">
      <v-steps direction="vertical" :current="1">
        <v-step title="" description=""></v-step>
        <v-step title="" description=""></v-step>
        <v-step title="" description=""></v-step>
        <v-step title="" description=""></v-step>
      </v-steps>
    </example>
    <example title="">
      <v-steps size='small' direction="vertical" :current="1">
        <v-step title="" description=""></v-step>
        <v-step title="" description=""></v-step>
        <v-step title="" description=""></v-step>
      </v-steps>
    </example>
    <example title="">
      <v-steps>
        <v-step title="" status="finish" description=""></v-step>
        <v-step title="" status="process" description=""></v-step>
        <v-step title="" status="wait" description=""></v-step>
        <v-step title="" status="wait" description=""></v-step>
      </v-steps>
    </example>
  </demo>
</template>
<script>
  import vSteps from '../../components/steps/Step'
  import vStep from '../../components/steps/StepItem'
  import vButton from '../../components/button'

  export default {
    data () {
      return {
        steps: 3,
        currentStep: 0
      }
    },
    components: {
      vSteps,
      vStep,
      vButton
    },
    beforeCompile () {
      this.currentStep = Math.floor(Math.random() * this.steps)
    },
    methods: {
      _next () {
        let s = this.currentStep + 1
        if (s === this.steps) {
          s = 0
        }
        this.currentStep = s
      }
    }
  }
</script>



