    </example>
    <example title="">
      <v-tag closable type="info"></v-tag>
      <v-tag closable type="primary"></v-tag>
      <v-tag closable type="warning"></v-tag>
      <v-tag closable type="danger"></v-tag>
    </example>
  </demo>
</template>
<script>
