<template lang="md">
  # Badge

  ---

  

  ## 

  

  ## API

  |            |                              |        |   |  |
  |----------------|----------------------------------|------------|---------|--------|
  | count          |  overflowCount  `${overflowCount}+` 0  | Number     |         |        |
  | overflowCount  |  | Number     |         |  99  |
  | dot            |      | boolean    |         | false  |

  ## 

  <demo>
    <example title="">
      <v-badge :count="5">
        <a href="#" class="head-example"></a>
      </v-badge>
    </example>
    <example title="">
      <v-badge :count="99">
        <a href="#" class="head-example"></a>
      </v-badge>
      <v-badge :count="100">
        <a href="#" class="head-example"></a>
      </v-badge>
    </example>
    <example title="">
      <v-badge :count="100">
        <a href="#" class="head-example"></a>
      </v-badge>
      <v-badge :count="100" :overflow-count="999">
        <a href="#" class="head-example"></a>
      </v-badge>
    </example>
    <example title="">
      <v-badge :dot="true">
        <a href="#" class="head-example"></a>
      </v-badge>
      <v-badge :dot="true">
        <a href="#"></a>
      </v-badge>
      <v-badge :dot="true">
        <a href="#"><i class="icon icon-github"></i></a>
      </v-badge>
    </example>
    <example title="">
      <v-badge :count="count">
        <a href="#" class="head-example"></a>
      </v-badge>
      <v-badge :dot="state">
        <a href="#" class="head-example"></a>
      </v-badge>
      <br/>
      <v-button type="default" size="small" @click="_plus()">+</v-button>
      <v-button type="default" size="small" @click="_sub()">-</v-button>
      <v-button type="default" size="small" @click="_changestate()"></v-button>
    </example>
  </demo>
</template>
<script>
import vBadge from '../../components/badge'
import vButton from '../../components/button'
export default {
  components: {
    vBadge,
    vButton
  },
  data () {
    return {
      count: 5,
      state: true
    }
  },
  methods: {
    _changestate () {
      var self = this
      self.state = !self.state
      return self.state
    },
    _plus () {
      var self = this
      self.count += 1
      return self.count
    },
    _sub () {
      var self = this
      self.count -= 1
      return self.count
    }
  }
}
</script>
<style>
  .head-example{
    width: 42px;
    height: 42px;
    border-radius: 6px;
    background: #eee;
    display: inline-block;
  }
  .vbadge{
    margin: 0 1rem .5rem 0;
  }
</style>



