<template lang="md">
  # Button

  ---

  

  ## 

  

  ## 

  -  Button `type` -> `shape` -> `size` -> `loading` -> `disabled`

  - 

  | |  |  | 
  |-----|-----|-----|------
  | type |  `primary` `default` `success` `info` `warning` `danger`  | Enum | undefined
  | htmlType |  `button`  `type`  HTML | Enum | `button`
  | shape |  `circle` `circle-outline`  | Enum | undefined
  | size |  `xsmall` `small` `large`  | Enum | undefined
  | loading |  `true` `false``loading="true"` | Bool | false
  | onClick | `click`  handler | Function | `function() {}`

  -  `<button></button>`

  ## 

  <demo>
    <example title="">
      <v-button type="primary"></v-button>
      <!-- <v-button></v-button> -->
      <v-button type="default"></v-button>
      <br />
      <v-button type="success"></v-button>
      <v-button type="info"></v-button>
      <v-button type="warning"></v-button>
      <v-button type="danger"></v-button>
    </example>
    <example title="">
      <v-button type="primary" size="xsmall"></v-button>
      <v-button type="primary" size="small"></v-button>
      <v-button type="primary" ></v-button>
      <v-button type="primary" size="large"></v-button>
    </example>
    <example title="">
      <v-button type="primary" shape='circle' size="large"><v-icon type='question'></v-icon></v-button>
      <v-button type="primary" shape='circle'><v-icon type='question'></v-icon></v-button>
      <v-button type="primary" shape='circle' size="small"><v-icon type='question'></v-icon></v-button>
      <v-button type="primary" shape='circle' size="xsmall"><v-icon type='question'></v-icon></v-button>
      <br />
      <v-button type="default" shape='circle-outline' size="large"><v-icon type='question'></v-icon></v-button>
      <v-button type="default" shape='circle-outline'><v-icon type='question'></v-icon></v-button>
      <v-button type="default" shape='circle-outline' size="small"><v-icon type='question'></v-icon></v-button>
      <v-button type="default" shape='circle-outline' size="xsmall"><v-icon type='question'></v-icon></v-button>
    </example>
    <example title="">
      <v-button type="primary"></v-button>
      <!-- <v-button></v-button> -->
      <v-button type="default"></v-button>
      <br />
      <v-button type="primary" disabled>()</v-button>
      <!-- <v-button disabled>()</v-button> -->
      <v-button type="default" class="disabled">()</v-button>
    </example>
    <example title="">
      <v-button type="primary" :loading="true" size="small"></v-button>
      <v-button type="primary" :loading="true"></v-button>
      <v-button type="primary" size="large" :loading="true"></v-button>
      <v-button type="primary" :loading="loading" @click="_enterLoading">(2s)</v-button>
    </example>
    <example title="">
      <v-button-group>
        <v-button type="default">1</v-button>
        <v-button type="default">2</v-button>
        <v-button type="default">3</v-button>
      </v-button-group>
    </example>
  </demo>
</template>
<script>
import vButton from '../../components/button/Button'
import vButtonGroup from '../../components/button/ButtonGroup'
import vIcon from '../../components/iconfont'
export default {
  data () {
    return {
      loading: false
    }
  },
  components: {
    vButton,
    vButtonGroup,
    vIcon
  },
  methods: {
    _enterLoading () {
      var self = this
      self.loading = true
      setTimeout(function (val) {
        self.loading = false
        return self.loading
      }, 2000)
    }
  }
}
</script>



