<template lang="md">
  # Form

  ---

  

  ## 

   form 
  - 
  - 
  - 

  ## 

  `<v-form-item></v-form-item>`

  ```html
  <v-form-item {...props}>
    {slot}
  </v-form-item>
  ```

  ## API

  Form

  | |  |  | 
  |-----|-----|-----|------
  | type |  `inline` `horizontal`  | String | undefined

  Form-item

  | |  |  | 
  |-----|-----|-----|------
  | title | label  | String | undefined
  | required |  | Boolean | false
  | waiting |  | Boolean | false
  | wrong |  | Boolean | false
  | warning |  | Boolean | false
  | success |  | Boolean | false
  | content |  | Boolean | false

  ## 

  <demo>
    <example title="">
      <v-form>
        <v-form-item title="Email address">
          <v-input placeholder="Email"></v-input>
        </v-form-item>
        <v-form-item title="Password">
          <v-input placeholder="Password" html-type="password"></v-input>
        </v-form-item>
        <v-form-item>
          <v-checkbox>Remember me</v-checkbox>
        </v-form-item>
        <v-form-item>
          <v-button type="default" html-type="submit">Submit</v-button>
        </v-form-item>
      </v-form>
    </example>
    <example title="">
      <v-form type="inline">
        <v-form-item>
          <v-input placeholder="Email" icon="user"></v-input>
        </v-form-item>
        <v-form-item>
          <v-input placeholder="Password" html-type="password"  icon="lock"></v-input>
        </v-form-item>
        <v-form-item>
          <v-button type="primary" html-type="submit">Login</v-button>
        </v-form-item>
      </v-form>
      <br />
      <v-form type="inline">
        <v-form-item>
          <v-input :group="true" addon-before="$" addon-after=".00" addon="addon" html-type="number"></v-input>
        </v-form-item>
        <v-form-item>
          <v-button type="primary" html-type="submit">Submit</v-button>
        </v-form-item>
      </v-form>
    </example>
    <example title="">
      <v-form type="horizontal">
        <v-form-item title="Email address">
          <v-input placeholder="Email"></v-input>
        </v-form-item>
        <v-form-item title="Password">
          <v-input placeholder="Password" html-type="password"></v-input>
        </v-form-item>
        <v-form-item>
          <v-checkbox>Remember me</v-checkbox>
        </v-form-item>
        <v-form-item>
          <v-button type="primary" html-type="submit">Submit</v-button>
        </v-form-item>
      </v-form>
    </example>
    <example title="">
      <v-modal title="" :visible.sync="visible1">
        <v-form type="horizontal">
          <v-form-item title="Email address" :required="true">
            <v-input placeholder="Email"></v-input>
          </v-form-item>
          <v-form-item title="Password">
            <v-input placeholder="Password" html-type="password"></v-input>
          </v-form-item>
          <v-form-item>
            <v-checkbox>Remember me</v-checkbox>
          </v-form-item>
        </v-form>
      </v-modal>
      <v-button type="primary" @click="visible1 = !visible1">show Modal</v-button>
    </example>
    <example title="">
      <v-form type="horizontal">
        <v-form-item title="Default" content="Default Input">
          <v-input placeholder="Default"></v-input>
        </v-form-item>
        <v-form-item title="Waiting" :waiting="true">
          <v-input placeholder="Waiting"></v-input>
        </v-form-item>
        <v-form-item title="Success" :success="true" content="The email is true">
          <v-input placeholder="Success input"></v-input>
        </v-form-item>
        <v-form-item title="Warning" :warning="true">
          <v-input placeholder="Warning"></v-input>
        </v-form-item>
        <v-form-item title="Fail" :wrong="true" content="Should be combination of numbers & alphabets">
          <v-input placeholder="unavailable choice"></v-input>
        </v-form-item>
        <v-form-item title="Wrong" :wrong="true" content="the date is wrong">
          <v-datepicker :start-date.sync="startTime" :end-date.sync="endTime" :range-picker="true"></v-datepicker>
        </v-form-item>
      </v-form>
    </example>
  </demo>

</template>
<script>
import vForm from '../../components/form/Form'
import vFormItem from '../../components/form/FormItem'
import vInput from '../../components/input'
import vButton from '../../components/button'
import vCheckbox from '../../components/checkbox'
import vModal from '../../components/modal'
import vDatepicker from '../../components/datepicker'
export default {
  components: {
    vForm,
    vFormItem,
    vInput,
    vButton,
    vCheckbox,
    vModal,
    vDatepicker
  },
  data () {
    return {
      visible1: false,
      startTime: '2017-01-01',
      endTime: ''
    }
  }
}
</script>



