<template lang="md">
  # Modal

  ---

  

  ## 

   Modal 

  ## API - Modal

  |       |                                      |           |  |
  |-----------|-----------------------------------------|---------------|--------|
  | visible   |                             | Boolean        | false |
  | title     |                                     | String         |      |
  | closable  |                     | Boolean        | true   |
  | okText    |                               | String         |    |
  | cancelText|                               | String         |    |
  | confirmLoading |  loading                    | Boolean        | false  |
  | onOk      |                               | function(e)    |      |
  | onCancel  |           | function(e)    |      |
  | footer    | false | Boolean        | false  |
  | maskClosable |                      | Boolean        | false  |
  | size      | lgsm                         | String         |   |

  ## API - confirmBox (modal="confirmBox")

  |       |                                      |           |  |
  |-----------|-----------------------------------------|---------------|--------|
  | visible   |                             | Boolean        | false |
  | title     |                                     | String         |      |
  | content   |                                     | String         |      |
  | okText    |                               | String         |    |
  | cancelText|                               | String         |    |
  | confirmLoading |  loading                    | Boolean        | false  |
  | onOk      |                               | function(e)    |      |
  | onCancel  |           | function(e)    |      |
  | maskClosable |                      | Boolean        | false  |

  <demo>
    <example title="">
      <v-modal title="" :visible.sync="visible1">
        <p>modal</p>
      </v-modal>
      <v-button type="primary" @click="visible1 = !visible1"></v-button>
    </example>
    <example title="">
      <v-modal title="" :visible.sync="visible2" ok-text="Submit" cancel-text="Return">
        <p>modal</p>
      </v-modal>
      <v-button type="primary" @click="visible2 = !visible2"></v-button>
    </example>
    <example title="">
      <v-modal title="" :visible.sync="visible3" :confirm-loading.sync="confirmLoading" :on-ok="_handleOk">
        <p>modal</p>
      </v-modal>
      <v-button type="primary" @click="visible3 = !visible3"></v-button>
    </example>
    <example title="">
      <v-modal title="" :visible.sync="visible4" :footer="false" :mask-closable="true" :closable="false">
        <p>modal</p>
      </v-modal>
      <v-button type="primary" @click="visible4 = !visible4"></v-button>
    </example>
    <example title="">
      <v-modal title="Modal" :visible.sync="visible5" size="lg">
        <p>modal</p>
      </v-modal>
      <v-button type="primary" @click="visible5 = !visible5">Modal</v-button>
      <v-modal title="Modal" :visible.sync="visible6" size="sm">
        <p>modal</p>
      </v-modal>
      <v-button type="primary" @click="visible6 = !visible6">Modal</v-button>
    </example>
    <example title="">
      <v-modal title="" :visible.sync="visible7" modal="confirmBox" content=""></v-modal>
      <v-button type="default" @click="visible7 = !visible7"></v-button>
    </example>
    <example title="">
      <v-modal title="" :visible.sync="visible8" modal="confirmBox" content="" :confirm-loading.sync="confirmLoading1" :on-ok="_confirm_handleOk"></v-modal>
      <v-button type="default" @click="visible8 = !visible8"></v-button>
    </example>
    <example title="">
      <v-modal title="Info" :visible.sync="visible9" modal="confirmBox" content="some messages...some messages..." :confirm-loading.sync="confirmLoading1" :on-ok="_confirm_handleOk" type="info"></v-modal>
      <v-button type="default" @click="visible9 = !visible9">Info</v-button>
      <v-modal title="Success" :visible.sync="visible10" modal="confirmBox" content="some messages...some messages..." :confirm-loading.sync="confirmLoading1" :on-ok="_confirm_handleOk" type="check"></v-modal>
      <v-button type="default" @click="visible10 = !visible10">Success</v-button>
      <v-modal title="Error" :visible.sync="visible11" modal="confirmBox" content="some messages...some messages..." :confirm-loading.sync="confirmLoading1" :on-ok="_confirm_handleOk" type="cross"></v-modal>
      <v-button type="default" @click="visible11 = !visible11">Error</v-button>
      <v-modal title="Warning" :visible.sync="visible12" modal="confirmBox" content="some messages...some messages..." :confirm-loading.sync="confirmLoading1" :on-ok="_confirm_handleOk" type="exclamation"></v-modal>
      <v-button type="default" @click="visible12 = !visible12">Warning</v-button>
    </example>
  </demo>
</template>
<script>
    import vModal from '../../components/modal'
    import vButton from '../../components/button'
    export default {
      data () {
        return {
          visible1: false,
          visible2: false,
          visible3: false,
          visible4: false,
          visible5: false,
          visible6: false,
          visible7: false,
          visible8: false,
          visible9: false,
          visible10: false,
          visible11: false,
          visible12: false,
          confirmLoading: false,
          confirmLoading1: false
        }
      },
      components: {
        vModal,
        vButton
      },
      methods: {
        _handleOk () {
          var self = this
          self.confirmLoading = true
          setTimeout(() => {
            self.visible3 = false
            self.confirmLoading = false
          }, 2000)
        },
        _confirm_handleOk () {
          var self = this
          self.confirmLoading1 = true
          setTimeout(() => {
            self.visible8 = false
            self.confirmLoading1 = false
          }, 2000)
        }
      }
    }
</script>



