<template lang="md">
  # Transfer

  ---

  

  ## 

  

    source targetAPI 

  ## 

  ```html
  data () {
    return {
      titles: ['Source', 'Target'],
      operations: ['To Left', 'To Right'],
      datas: [],
      targets: []
    }
  },
  created () {
    var self = this
    const mockData = []
    for (let i = 0; i < 20; i++) {
      mockData.push({
        key: i,
        title: `content${i + 1}`,
        description: `description of content${i + 1}`,
        disabled: i % 3 < 1
      })
    }
    self.datas = mockData
    const targetKeys = mockData
      .filter(item => +item.key % 3 > 1)
      .map(item => item.key)
    self.targets = targetKeys
  }
  ```

  ## API

  |        |          |            |       |
  |------------|--------------|-------------|--------------|
  | titles | , | Array | undefined |
  | operations | , | Array | undefined |
  | dataSource | `targetKeys` | Array | [] |
  | targetKeys | key | Array | [] |
  | onchange |  | Function | () => {} |
  | showFilter |  | Boolean | false |
  | showDescription |  | Boolean | false |
  | showReload | `clear all``Reload` | Boolean | false |
  | searchPlaceholder |  | String | 'Search' |
  | notFoundContent |  | String | 'Not Result' |

  ## 

  <demo>
    <example title="">
      <v-transfer :titles="titles" :data-source="datas" :target-keys="targets"></v-transfer>
    </example>
    <example title="">
      <v-transfer :titles="titles" :data-source="datas" :target-keys="targets" :show-filter="true"></v-transfer>
    </example>
    <example title="">
      <v-transfer :titles="titles" :data-source="datas" :target-keys="targets" :show-filter="true" :show-description="true"></v-transfer>
    </example>
    <example title="">
      <v-transfer :titles="titles" :data-source="datas" :target-keys="targets" :show-filter="true" :show-reload="true"></v-transfer>
    </example>
    <example title="">
      <v-transfer :titles="titles" search-placeholder="" :data-source="datas" :target-keys="targets" :show-filter="true" :operations="operations"></v-transfer>
    </example>
  </demo>
</template>
<script>
  import vTransfer from '../../components/transfer'
  export default{
    data () {
      return {
        titles: ['Source', 'Target'],
        operations: ['To Left', 'To Right'],
        datas: [],
        targets: []
      }
    },
    components: {
      vTransfer
    },
    created () {
      var self = this
      const mockData = []
      for (let i = 0; i < 20; i++) {
        mockData.push({
          key: i,
          title: `content${i + 1}`,
          description: `description of content${i + 1}`,
          disabled: i % 3 < 1
        })
      }
      self.datas = mockData
      const targetKeys = mockData
        .filter(item => +item.key % 3 > 1)
        .map(item => item.key)
      self.targets = targetKeys
    }
  }
</script>



