<template lang="md">
  # Radio

  ---

  

  ## 

  - 
  -  Select Radio 

  ## API

  Radio

  |            |                                      |        |   |  |
  |----------------|------------------------------------------|------------|---------|--------|
  | checked        |                          | Boolean    |         | false  |
  | defaultChecked |                              | Boolean    |         | false  |
  | value          |  value         | String     |         |      |

  RadioGroup

   `Radio`

  |            |                              |               |  |  |
  |----------------|----------------------------------|-------------------|--------|--------|
  | onChange       |              | Function(e:Event) |      |      |
  | value          |              | String            |      |      |
  | defaultValue   |                      | String            |      |      |

  ## 

  <example title="">
    <v-radio :default-checked="checked"></v-radio>
  </example>
  <example title="">
    <v-radio :disabled="disabled"></v-radio>
    <v-radio :checked.sync="checked" :disabled="disabled"></v-radio>
    <br />
    <v-button type="default" @click="_toggleChange"></v-button>
  </example>
  <example title="RadioGroup ">
    <v-radio-group :on-change="_onGroupChange" :value="groupValue" :radios="radios"></v-radio-group>
    <p>{{groupValue}}</p>
  </example>
  <example title="RadioGroup ">
    <v-radio-group type="button" :on-change="_onCityChange" :value="cityValue" :radios="radios1"></v-radio-group>
    <p>{{cityName}}</p>
  </example>
</template>
<script>
  import vRadio from '../../components/radio/Radio'
  import vRadioGroup from '../../components/radio/RadioGroup'
  import vButton from '../../components/button'

  export default {
    data () {
      return {
        checked: true,
        disabled: true,
        radios: [{
          value: 'a',
          name: 'A'
        }, {
          value: 'b',
          name: 'B'
        }, {
          value: 'c',
          name: 'C'
        }, {
          value: 'd',
          name: 'D'
        }],
        groupValue: 'a',
        radios1: [{
          value: 'a',
          name: ''
        }, {
          value: 'b',
          name: ''
        }, {
          value: 'c',
          name: ''
        }, {
          value: 'd',
          name: ''
        }],
        cityValue: 'a',
        cityName: ''
      }
    },
    components: {
      vRadio,
      vRadioGroup,
      vButton
    },
    methods: {
      _toggleChange () {
        var self = this
        self.disabled = !self.disabled
      },
      _onGroupChange (e) {
        console.log('radio checked:' + e.target.value)
        this.groupValue = e.target.value
      },
      _onCityChange (e) {
        console.log('city checked:' + e.target.name)
        this.cityName = e.target.name
      }
    }
  }
</script>



