<template lang="md">
  # Input

  ---

  

  ## 

  - 
  - 

  ## 

  | |  |  | 
  |-----|-----|-----|------
  | group |  | String | false
  | size | `small``large` | String | undefined
  | placeholder | placeholder | String | undefined
  | addon | type`addon``button``dropdown``checkbox``radio` | String | undefined
  | addonBefore |  | String | undefined
  | addonAfter |  | String | undefined
  | icon |  | String | undefined
  | htmlType |  `input`  `type`  HTML | String | `text`
  | type | inputtextarea`input``textarea` | String | `input`
  | rows | textarea`type = textarea` | Number | 4
  | maxLength |  | Number | undefined
  | onClick | `click`  handler | Function | `function() {}`

  ## 

  <demo>
    <example title="">
      <v-input placeholder=""></v-input>
      <v-input placeholder="" icon="user"></v-input>
      <v-input placeholder="" icon="lock" html-type="password"></v-input>
    </example>
    <example title="/">
      <v-input placeholder="" :group="true" addon-before="@" addon="addon"></v-input>
      <v-input placeholder="Username" icon="user" :group="true" addon-after="@example.com" addon="addon"></v-input>
      <v-input :group="true" addon-before="$" addon-after=".00" addon="addon" html-type="number"></v-input>
      <label>Your vanity URL</label>
      <v-input :group="true" addon-before="https://example.com/users/" addon="addon"></v-input>
    </example>
    <example title="/">
      <v-input placeholder="Username" :group="true" addon-after="Search" addon="button" :onclick="_search"></v-input>
      <!-- <v-input placeholder="Username" :group="true" addon-before="Search" addon="dropdown" :onclick="_search"></v-input> -->
    </example>
    <example title="">
      <v-input placeholder="" :group="true" addon-before="@" addon="addon" size="small"></v-input>
      <v-input placeholder="" :group="true" addon-before="@" addon="addon"></v-input>
      <v-input placeholder="" :group="true" addon-before="@" addon="addon" size="large"></v-input>
    </example>
    <example title="">
      <v-input type="textarea" placeholder="Autosize height based on content lines"></v-input>
      <v-input type="textarea" placeholder="Autosize height based on content lines" :rows="8" :max-length="1000"></v-input>
    </example>
  </demo>
</template>
<script>
import vInput from '../../components/input'
export default {
  components: {
    vInput
  },
  methods: {
    _search () {
      console.log("Searching!")
    }
  }
}
</script>
<style>
.code-box-demo:first-child .form-control{
  margin-bottom:8px;
}
</style>



