<template lang="md">
  # Menu

  ---

  

  ## 

  

  ## 

  ```html
  data () {
    return {
      data: [
        {'value': '', 'path': '/components/menu'},
        {'value': '', 'path': ''},
        {'value': '', 'path': '', 'disabled': true},
        {'value': 'Admin', 'path': '', 'dropdown': true, 'data': [
          {'value': '', 'path': ''},
          {'value': '', 'onclick': this._toggle}
        ]}
      ],
      data1: [
        {'value': '', 'path': '/components/menu', 'icon': 'team'},
        {'value': '', 'path': '', 'icon': 'save'},
        {'value': '', 'path': '', 'icon': 'folder'},
        {'value': '', 'path': '', 'icon': 'exception'},
        {'value': '', 'path': '', 'icon': 'solution'}
      ]
    }
  }
  ```

  ## API

  |  |  |  |  |
  |------|-----|------|-------|
  | type | `navbar`,`sidebar` | String | undefined
  | theme | `default`,`inverse` | String | `default`
  | data |  | Array | undefined
  | `slot="logo"` | logo | String | undefined
  | `slot="button"` | button | String | undefined

  ## 

  <demo>
    <example title="">
      <v-menu type="navbar" :data="data">
        <img src="../../assets/logo1.png" slot="logo" />
      </v-menu>
    </example>
    <example title="">
      <v-menu type="navbar" :data="data" theme="inverse">
        <img src="../../assets/logo2.png" slot="logo" />
      </v-menu>
    </example>
    <example title="">
      <p><v-switch :default-checked="checked" :on-change="_toggle"></v-switch></p>
      <v-menu type="sidebar" :data="data1" :theme="theme">
        <div slot="button" class="sidebar-btn">
          <v-button type="primary" :block="true" size="large"><v-icon type="edit"></v-icon></v-button>
        </div>
      </v-menu>
    </example>
  </demo>
</template>
<script>
  import vMenu from '../../components/menu'
  import vButton from '../../components/button'
  import vIcon from '../../components/iconfont'
  import vSwitch from '../../components/switch'

  export default {
    data () {
      return {
        data: [
          {'value': '', 'path': '/components/menu'},
          {'value': '', 'path': ''},
          {'value': '', 'path': '', 'disabled': true},
          {'value': 'Admin', 'path': '', 'dropdown': true, 'data': [
            {'value': '', 'path': ''},
            {'value': '', 'onclick': this._logout}
          ]}
        ],
        data1: [
          {'value': '', 'path': '', 'icon': 'team'},
          {'value': '', 'path': '/components/menu', 'icon': 'save'},
          {'value': '', 'path': '', 'icon': 'folder'},
          {'value': '', 'path': '', 'icon': 'exception'},
          {'value': '', 'path': '', 'icon': 'solution'}
        ],
        checked: false,
        theme: 'inverse'
      }
    },
    components: {
      vMenu,
      vButton,
      vIcon,
      vSwitch
    },
    methods: {
      _logout () {
        console.log("Log Out")
      },
      _toggle (val) {
        this.theme = val ? 'default' : 'inverse'
      }
    }
  }
</script>



