<template lang="md">
    # Checkbox

    ---

    

    ## 

    - /
    -  `switch` `switch`  `checkbox` 

    ## API

    ### Checkbox

          |                                      |        |   | |
    -----------|------------------------------------------|------------|-------|--------|
    checked |  | boolean  |   | false
    default-checked |  | boolean |  | false
    onchange |  | Function(e:Event) |  |

    ## 

    <demo>
      <example title="">
        <v-checkbox>Checkbox</v-checkbox>
        <v-checkbox :default-checked="checked" :onchange="_checkShow"><span>Checkbox</span></v-checkbox>
      </example>
      <example title="">
        <v-checkbox disabled>Checkbox</v-checkbox>
        <v-checkbox :default-checked="true" disabled><span>Checkbox</span></v-checkbox>
      </example>
      <example title="checkbox">
        <v-checkbox :checked="checked" :disabled="disabled"><span>Checkbox</span></v-checkbox>
        <br/>
        <v-button type="default" size="small" @click="_checkChange">check</v-button>
        <v-button type="default" size="small" @click="_disabledChange">disabled</v-button>
      </example>
    </demo>
</template>
<script>
import vCheckbox from '../../components/checkbox'
import vButton from '../../components/button'
export default{
  components: {
    vCheckbox,
    vButton
  },
  data () {
    return {
      checked: true,
      disabled: false
    }
  },
  methods: {
    _checkChange () {
      this.checked = !this.checked
    },
    _disabledChange () {
      this.disabled = !this.disabled
    },
    _checkShow (e) {
      console.log('checked=' + e.target.checked)
    }
  }
}
</script>



