<template lang="md">
  # Breadcrumb

  ---

  

  ## 

  - 
  - 
  - 

  ## 

  ```html
  data () {
    return {
      data: [{
        name: 'first',
        link: '/index'
      }, {
        name: 'second',
        link: '/index'
      }, {
        name: 'third',
        link: '',
        active: true
      }],
      data1: [{
        link: '/index',
        icon: 'home'
      }, {
        name: 'second',
        link: '/index',
        icon: 'user'
      }, {
        name: 'third',
        link: '',
        active: true
      }]
    }
  ```

  ## API

  |            |                              |        |  |
  |----------------|----------------------------------|------------|--------|
  | data           | iconnamelinkactive | Array |  |

  ## 
  <demo>
    <example title="">
      <v-breadcrumb :data="data"></v-breadcrumb>
    </example>
    <example title="">
      <v-breadcrumb :data="data1"></v-breadcrumb>
    </example>
  </demo>
</template>
<script>
  import vBreadcrumb from '../../components/breadcrumb'

  export default {
    data () {
      return {
        data: [{
          name: 'first',
          link: '/index'
        }, {
          name: 'second',
          link: '/index'
        }, {
          name: 'third',
          link: '',
          active: true
        }],
        data1: [{
          link: '/index',
          icon: 'home'
        }, {
          name: 'second',
          link: '/index',
          icon: 'user'
        }, {
          name: 'third',
          link: '',
          active: true
        }]
      }
    },
    components: {
      vBreadcrumb
    }
  }
</script>



