<template lang="md">
  # Datepicker

  ---

  /

  ## 

  /

  ## API

  |          |            |      |        |
  |--------------|----------------|----------|--------------|
  | Date         |         | string   |       |
  | startDate    |      | string   |       |
  | endDate      |      | string   |       |
  | format       |    | string   | "yyyy-MM-dd" |
  | showTime     |    | boolean  | false        |
  | disabled     |            | bool     | false        |

  <demo>
    <example title="">
      <v-datepicker :Date.sync="selectedTime1"></v-datepicker>
    </example>
    <example title="">
      <v-datepicker :Date.sync="selectedTime2" format="yyyy/mm/dd"></v-datepicker>
    </example>
    <example title="">
      <v-datepicker :Date.sync="selectedTime3" :show-time="true"></v-datepicker>
    </example>
    <example title="">
      <v-datepicker :Date.sync="selectedTime5" :disabled="true"></v-datepicker>
    </example>
    <example title="">
      <v-datepicker :start-date.sync="startTime1" :end-date.sync="endTime1" :range-picker="true"></v-datepicker>
    </example>
    <example title="">
      <v-datepicker :start-date.sync="startTime2" :end-date.sync="endTime2" :range-picker="true" :show-time="true"></v-datepicker>
    </example>
  </demo>
</template>
<script>
  import vDatepicker from '../../components/datepicker'
  export default {
    data () {
      return {
        selectedTime1: '',
        selectedTime2: '',
        selectedTime3: '',
        selectedTime4: '',
        selectedTime5: '',
        startTime1: '',
        endTime1: '',
        startTime2: '',
        endTime2: ''
      }
    },
    components: {
      vDatepicker
    }
  }
</script>



