<template lang="md">
  # Spin

  ---

  

  ## 

  

  ## API

  |        |            |       |          |
  |------------|----------------|-------------|--------------|
  | size       | enum           | default     | spin small large |
  | spinning   | boolean        | true        |  loading     |
  | tip    | string        |         |  |
  | delay | number () |  |  loading  |

  ## 
  <demo>
    <example title="">
      <div class="spin-box">
        <v-spin></v-spin>
      </div>
    </example>
    <example title="">
      <div class="spin-box">
        <v-spin tip="..."></v-spin>
      </div>
    </example>
    <example title="">
      <div class="spin-box">
        <v-spin size="small"></v-spin>
      </div>
      <div class="spin-box">
        <v-spin></v-spin>
      </div>
      <div class="spin-box">
        <v-spin size="large"></v-spin>
      </div>
    </example>
    <example title="">
      <v-alert type="info" message="">
        
        <v-spin :spinning="spinning"></v-spin>
      </v-alert>
      <v-alert type="info" message="">
        
        <v-spin :spinning="spinning" tip="..."></v-spin>
      </v-alert>
      
      <v-switch :checked.sync="spinning"></v-switch>
    </example>
  </demo>
</template>
<script>
  import vSpin from '../../components/spin'
  import vAlert from '../../components/alert'
  import vSwitch from '../../components/switch'
  export default {
    data () {
      return {
        spinning: true
      }
    },
    components: {
      vSpin,
      vAlert,
      vSwitch
    }
  }
</script>
<style>
.spin-box {
  width: 100%;
  height: 50px;
  position: relative;
}
</style>



