<template lang="md">
  # Tabs

  ---

  

  ## 

  

  ## API

  | |  |  | 
  |-----|-----|-----|------
  | type | `tabs``pills` | String | 
  | stacked | `type=pills` | Boolean | false
  | justified |  | Boolean | false
  | data | `link``value``active``disabled``showdrop``dropdown` | Array | 

  ## 

  <demo>
    <example title="">
      <v-tabs type="tabs" :data="data"></v-tabs>
    </example>
    <example title="">
      <v-tabs type="pills" :data="data"></v-tabs>
    </example>
    <example title="">
      <v-tabs type="pills" :stacked="true" :data="data"></v-tabs>
    </example>
    <example title="">
      <v-tabs type="tabs" :justified="true" :data="data"></v-tabs>
      <br/>
      <v-tabs type="pills" :justified="true" :data="data"></v-tabs>
    </example>
    <example title="">
      <v-tabs type="tabs" :data="data1"></v-tabs>
    </example>
  </demo>
</template>
<script>
  import vTabs from '../../components/tabs'

  export default {
    data () {
      return {
        data: [{
          link: '',
          value: '111'
        }, {
          link: '',
          value: '222'
        }, {
          link: '',
          value: '333',
          disabled: true
        }],
        data1: [{
          link: '',
          value: '111',
          active: true
        }, {
          link: '',
          value: '222'
        }, {
          link: '',
          value: '333',
          showdrop: true,
          dropdown: [{
            value: '111',
            onclick: function _showlog () {
              console.log("show log!")
            }
          }, {
            link: '/',
            value: '222'
          }, {
            disabled: true,
            link: '',
            value: '333'
          }]
        }]
      }
    },
    components: {
      vTabs
    }
  }
</script>



